    form {
        width: 100%;
        max-width: 450px;
        display: flex;
        flex-direction: column;
        // background-color: #4e96de;
        padding: 3em;
        border-radius: 50px;
        margin-right: 10em;

        input,
        textarea {
            margin-bottom: 1.5em;
            border: none;
            border-radius: 10px;
            height: 40px;
            padding-left: 1em;
            box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);
        }

        textarea {
            padding-top: 1em;
            height: 100px;
        }

        p {
            color: #fcc5c5;
            font-size: 0.8rem;
            margin: 0;
            margin-bottom: 20px;
            margin-top: -15px;
            font-weight: 500;
        }

        input[type="submit"] {
            cursor: pointer;
            width: 80%;
            margin: 0 auto;
            background-color: v.$themePurple;
            color: white;
            border-radius: 20px;
        }
    }
}
