.wrapper {
    margin-top: -32px;
    header {
        margin-block: 2em;
    }

    .button {
        display: flex;
        width: 100%;
        justify-content: center;
    }

    .hero {
        line-height: 30px;
        width: 100%;
        // margin-bottom: 10em;
        background-color: #f8f7ff;
        padding-block: 1.5em;

        .image {
            display: flex;
            width: 100%;
            justify-content: center;

            img {
                width: 300px;
                height: auto;
                border-radius: 50%;
            }
        }

        p {
            line-height: 40px;
            margin-bottom: 2em;
        }
    }
    .teamSection {
        article {
            border-radius: 20px;
            box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.2);
            img {
                border-radius: 20px;
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
                width: 100%;
                height: 320px;
                object-fit: cover;
            }

            .bottom {
                padding-inline: 1em;
                padding-block: 1em;

                .designation {
                    color: rgba(1, 1, 1, 0.8);
                }
            }
        }
    }

    button {
        margin-block: 2em;
        width: 100px;
        font-weight: 500;
    }

    ul {
        padding-left: 0;
        list-style-type: none;

        svg {
            color: #1f97f2;
            margin-right: 5px;
        }
        li {
            padding-block: 0.2em;
            display: flex;
            align-items: center;
        }

        div {
            font-weight: 500;
            margin-bottom: 0.5em;
        }
    }
}
