.wrapper {
    position: relative;
    max-height: 650px;
    width: 900px;
    position: relative;
    padding: 3em;

    display: flex;
    justify-content: flex-end;
    padding-right: 100px;

    .graphic {
        position: absolute;

        &.boy {
            left: 15px;
            transform: rotateY(180deg);
            top: 100px;
            img {
                width: 180px;
            }
        }

        &.bgLeft {
            left: 0;
            top: 0;
            height: 100%;

            img {
                height: 100%;
            }
        }
    }
}

@media screen and (max-width: 850px) {
    .wrapper {
        width: 95vw;
        padding: 2em;
        padding-right: 2em;
        padding-top: 4em;

        .graphic {
            display: none;
        }
    }
}
