@use "@utils/variables" as v;

.wrapper {
    cursor: pointer;
    background-color: v.$themePurple;
    color: white;
    border: none;
    border-radius: 20px;
    height: 38px;
    padding-inline: 1.4rem;
}

.wrapper.light {
    background-color: white;
    border: 1px solid v.$themePurple;
    color: v.$themePurple;
}
