.wrapper {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(1, 1, 1, 0.3);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;

    .modalWrapper {
        background-color: white;
        border-radius: 15px !important;
        position: relative;

        .contentWrapper {
            &::-webkit-scrollbar {
                width: 10px;
            }

            /* Track */
            &::-webkit-scrollbar-track {
                background: white;
                border-top-right-radius: 15px;
                border-bottom-right-radius: 15px;
            }

            /* Handle */
            &::-webkit-scrollbar-thumb {
                background: rgb(175, 175, 175);
                border-top-right-radius: 15px;
                border-bottom-right-radius: 15px;
            }

            /* Handle on hover */
            &::-webkit-scrollbar-thumb:hover {
                background: rgb(141, 141, 141);
            }
        }

        .btnCross {
            position: absolute;
            top: 20px;
            right: 20px;
            cursor: pointer;
            font-size: 1.5rem;
            color: rgba(1, 1, 1, 0.5);
            border: 1px solid rgba(1, 1, 1, 0.1);
            border-radius: 50%;
            width: 30px;
            height: 30px;
            background-color: white;
            display: flex;
            justify-content: center;
            align-items: center;

            z-index: 3;

            &:hover {
                box-shadow: 1px 1px 15px rgba(1, 1, 1, 0.2);
            }
        }
    }
}
